/*################################*\
    legacy  |  _base.less
\*################################*/
html {
  --site-logo-size: 6rem;
  --site-logo-space: 0;
  --year-badge-width: 10vw;
  --supporter-logo-sml-width: 68px ;
  --supporter-logo-sml-height: 36px ;
  --supporter-logo-med-width: 90px ;
  --supporter-logo-med-height: 48px ;
  --supporter-logo-lrg-width: 120px;
  --supporter-logo-lrg-height: 65px ;
}
@media screen and (min-width: 30em) {
  html {
    --supporter-logo-sml-width: 140px;
    --supporter-logo-sml-height: 62px ;
    --supporter-logo-med-width: 188px;
    --supporter-logo-med-height: 82px ;
    --supporter-logo-lrg-width: 250px;
    --supporter-logo-lrg-height: 110px;
  }
}
@media screen and (min-width: 45em) {
  html {
    --site-logo-space: 1rem;
    --side-col-size: 40%;
  }
}
@media screen and (min-width: 60em) {
  html {
    --side-col-size: 35%;
    --supporter-logo-sml-width: 202px;
    --supporter-logo-sml-height: 90px ;
    --supporter-logo-med-width: 270px;
    --supporter-logo-med-height: 120px;
    --supporter-logo-lrg-width: 360px;
    --supporter-logo-lrg-height: 160px;
  }
}
@media screen and (min-width: 75em) {
  html {
    --site-logo-space: 2rem;
  }
}
@media screen and (min-width: 90em) {
  html {
    --side-col-size: 30%;
    --year-badge-width: 9rem;
  }
}
@media screen and (min-width: 45em) and (max-width: 74.999em) {
  html {
    --site-logo-size: 4rem;
  }
}
body {
  font-family: 'Open Sans', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  color: #333;
}
main {
  margin-bottom: 3rem;
  margin-bottom: calc(2 * var(--lh));
  margin-bottom: 2lh;
}
@supports (margin-block-end: var(--v)) {
  main {
    margin-bottom: unset;
    margin-block-end: calc(2 * var(--lh));
    margin-block-end: 2lh;
  }
}
main h1,
main h2,
main h3,
main h4,
main h5,
main h6 {
  font-family: 'futura-pt', 'Open Sans', 'Helvetica Neue', 'Helvetica', sans-serif;
  color: var(--color-primary, #6a9556);
}
@media not all and (min-width: 45em) {
  main {
    flex-direction: column;
  }
}
figcaption {
  font-weight: 300;
  font-style: italic;
  font-size: calc(var(--font-scale, 1) * 1em);
  line-height: calc(var(--line-height) / var(--font-scale, 1) * var(--tracks, 1));
  --font-scale: 0.75;
}
@media (-ms-high-contrast: none), (-ms-high-contrast: active) {
  figcaption {
    font-size: 0.75em;
    line-height: 2;
  }
}
ol,
ul {
  padding-left: 2rem;
}
@supports (padding-inline-start: 1rem) {
  ol,
  ul {
    padding-left: unset;
    padding-inline-start: 2rem;
  }
}
thead,
tfoot {
  background-color: #f4f4f5;
  font-weight: 600;
  font-size: 0.75em;
}
th,
td {
  padding: 10px;
  border: 1px solid #e8eaeb;
}
table.striped tbody > tr:nth-child(even) {
  background-color: #f4f4f5;
}
tr.striped {
  background-color: #f4f4f5;
}
table.bordered th,
table.bordered td {
  border: 1px solid #e8eaeb;
}
a[href] {
  color: #0c5fa8;
}
a[href]:hover,
a[href]:focus {
  color: #19a1cf;
}
